html, body {
    background-color: $clrBodyBg;
    font-family: 'Helvetica Neue', sans-serif;
}

body {
    min-width: 800px;
}

a {
    color: $clrLink;
    cursor: pointer;
    text-decoration: none;
    &:hover {
        color: lighten($clrLink, $clrHoverLightenPercent);
    }
}

.list-vertical {
    margin-bottom: $interiorMargin * 2 !important;
}


nav,
h1, h2, h3,
a.arw, ul.link-list.arw li a, .l-hero-text, .t-footer, .t-list-steps:not(code) {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    letter-spacing: -0.03em;
}

nav,
h2, h3,
.t-footer {
    text-transform: uppercase;
}

h2, h3 {
    &:first-child {
        margin-top: 0;
    }
}

.h2 {  @extend h2;  }
.h3 {  @extend h3;  }

.padX {
    box-sizing: border-box;
    padding: 0 $marginH;
}

/******************************************** SYMBOLS */
@font-face {
    font-family: 'omct-symbols';
    src:    url('../fonts/omct-symbols.eot?yyn1qj');
    src:    url('../fonts/omct-symbols.eot?yyn1qj#iefix') format('embedded-opentype'),
    url('../fonts/omct-symbols.ttf?yyn1qj') format('truetype'),
    url('../fonts/omct-symbols.woff?yyn1qj') format('woff'),
    url('../fonts/omct-symbols.svg?yyn1qj#omct-symbols') format('svg');
    font-weight: normal;
    font-style: normal;
}

/******************************************** WRAPPERS */
.wo {
    // Wrapper outer
    width: 100%;
}

.wi {
    // Wrapper inner
    @extend .padX;
    max-width: $maxW;
    margin: 0 auto;
}

.w-image {
    // Wraps an image, allows abspos element to be centered over the image
    position: relative;
}

/******************************************** ARCHETYPES */

.btn {
    white-space: nowrap;
    &.major {
        $clrHvr: lighten($clrBtnMajor, 10%);
        @include linear-gradient(lighten($clrBtnMajor, 10%), $clrBtnMajor);
        border-radius: $radiusBasic;
        box-shadow: rgba(black, 0.4) 0 2px 10px;
        color: white;
        display: inline-block;
        line-height: normal;
        padding: 10px 20px;
        &:hover {
            @include linear-gradient(lighten($clrHvr, $clrHoverLightenPercent), $clrHvr);
        }
        &.overlaid {
            // The button finds itself wrapped in a warm, fuzzy
            // relpos element. If wants centering.
            font-size: 2.5rem;
            position: absolute;
            top: 50%; left: 50%;
            @include transform(translate(-50%, -50%));
        }
    }
}

a.arw {
    text-transform: uppercase;
    &:after {
        @extend .icon;
        content: ' \e900';
        font-size: 0.8em;
    }
}

ul.link-list {
    @extend .no-bullets;
    &.arw {
        li a {
            @extend a.arw;
        }
    }
}

.hero-pager {
    $h: 50px;
    position: absolute;
    top: auto; right: 0; bottom: 0; left: 0; height: $h;
    line-height: $h;
    text-align: center;
    z-index: 5;

    .pager-dot {
        $d: 12px;
        border-radius: 50%;
        box-shadow: 0 2px 20px 5px rgba(black, 1);
        cursor: pointer;
        display: inline-block;
        background: #999;
        height: $d; width: $d;
        &:not(:first-child) {
            margin-left: $interiorMargin;
        }
        &.active, &:hover { background: #fff; }
    }
}

/******************************************** LOGOS */
.logo {
    display: inline-block;
}

.logo-bg {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
}

/******************************************** EFFECTS AND TRANSITIONS */
.hidden {
    opacity: 0;
    pointer-events: none;
}

.trans-to-next {
    // Used by rotating heros
    @include transition-property(opacity);
    @include transition-duration(500ms);
    @include transition-timing-function(ease-in-out);
    @include transition-delay(0);
}

/******************************************** LAYOUT */
@mixin assymCols($numCols: 2, $biggerSide: right) {
    $w: 100%/$numCols;
    @if $biggerSide == right {
        .col.col-left { width: $w; }
        .col.col-right { width: 100% - $w; }
    } @else {
        .col.col-left { width: 100% - $w; }
        .col.col-right { width: $w; }
    }
}

.cols {
    display: flex;
    align-items: stretch;
    align-contents: stretch;


    .col {
        flex: 1 1 auto;
        margin-left: $marginH;
        padding-left: $marginH;
        &:first-child {
            margin-left: 0;
            padding-left: 0;
        }
        .c-wrap {
            @include flex(1 1 auto);
        }
        .c-wrap.graphic {
            @include flex(0 1 auto);
            margin-top: $interiorMargin;
            display: block;
        }
    }
    &-equal {
        .col {
            flex: 1 0 0px;
        }
    }

    &.cols-1-2 {  @include assymCols(3, right);  }
    &.cols-1-3 {  @include assymCols(4, right);  }
    &.cols-1-4 {  @include assymCols(5, right);  }
    &.cols-1-5 {  @include assymCols(6, right);  }
    &.cols-2-1 {  @include assymCols(3, left);  }
    &.cols-3-1 {  @include assymCols(4, left);  }
    &.cols-4-1 {  @include assymCols(5, left);  }
    &.cols-5-1 {  @include assymCols(6, left);  }

    &--with-dividers {
        .col + .col {
            border-left: 1px solid #ccc;
        }
    }
}


.t-list-steps {
    b {
        color: lighten($clrBodyBg, 40%);
        display: inline-block;
        width: 20px;
    }
    .code-wrapper {
        background-color: #333;
        border-radius: $radiusBasic;
        border: none;
        box-sizing: border-box;
        color: #ccc;
        display: block;
        font-family: monospace;
        padding: 10px 10px;
        width: 100%;
        &:focus {
            outline: none;
        }
        &::selection {
            background-color: $clrLink;
            color: white;
        }

        &::-moz-selection {
            background-color: $clrLink;
            color: white;
        }
    }
    label {
        opacity: 0;
        pointer-events: none;
        position: absolute;
    }
    li {
        margin-bottom: $interiorMarginLg;
    }
    p {
        font-size: 1.25em;
        text-transform: uppercase;
    }
}

.icon {
    font-family: 'omct-symbols' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
}

.video-wrapper {
    position: relative;
    height: 0;
    &.aspect16x9 { padding-bottom: 56.25%; }
    &.aspect3x1 { padding-bottom: 66.6%; }
    &.aspect4x3 { padding-bottom: 75%; }

    iframe.video {
        position: absolute;
        top: 0; left: 0;
        width: 100%;
        height: 100%;
    }
}

.area-scrolling {
    overflow: auto;
}

/******************************************** PAGE SECTIONS */
.l-page-section {
    //@extend .padX;
    background-color: white;
    color: $clrBodyBg;
    font-size: 0.9rem;
    .wi {
        padding-top: $marginV;
        padding-bottom: $marginV;
    }

    h1, h1 a, h2, h2 a, h3 { color: #666; }

    h1 {
        font-size: 2.25em;
        text-align: center;
        margin-bottom: $marginV;
    }

    h2 {
        font-size: 1.2em;
        font-weight: 400;
        margin-top: 0;
    }

    &.c-ltgray {
        background-color: #fbfbfb;
    }

    &.t-feature {
        @include linear-gradient(#fff 90%, #f6f6f6);
    }

    .area-scrolling {
        height: 200px;
        margin-bottom: $interiorMarginLg;
        width: 100%;
    }

    .t-intro {
        margin-bottom: $marginH;
    }


}

/******************************************** NAV */
nav {
    background: $clrBodyBg;
    height: $navH;
    position: fixed;
    z-index: 2;
}

.main-nav {
    @extend .padX;
    @include align-items(center);
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    font-size: 0.8rem;
    //height: 100%; // WTF is this for?

    a {
        $c: #ccc;
        color: $c;
        &:hover {
            color: lighten($c, $clrHoverLightenPercent);
        }
    }
    .logo-bg {
        flex-shrink: 0;
        height: $navH;
    }
}

.l-nav-links {
    @include trans-prop-nice($props: (height, min-height, padding-bottom, padding-top), $dur: 250ms);
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    flex: 2 1 auto;
    justify-content: flex-end;
    white-space: nowrap;
    .logo {
        vertical-align: middle;
    }
    .mobile-sm-only {
        display: none;
    }
}

.nav-link {
    display: inline-block;
    vertical-align: middle;
    &:not(:first-child) { margin-left: $padNavElem; }
}

/******************************************** SPECIFIC ELEMENTS */
.l-main {
    // Push down content to make room for main nav.
    // Don't do this if nav uses translucent look.
    padding-top: $navH;
}

.btn.download {
    $c: lighten($clrBtnMajor, 30%);
    &:after {
        @extend .icon;
        color: $c;
        content: ' \e901';
        font-size: .8em;
    }
    &:hover:after {
        color: lighten($c, $clrHoverLightenPercent);
    }
}

.logo-nasa {
    @extend .logo-bg;
    background-image: url('../images/logo-nasa.svg');
    width: $navH * .9;
}
.logo-openmct {
    @extend .logo-bg;
    background-image: url('../images/logo-openmct.svg');
    margin-left: $padNavElem;
    width: ($navH * .55) * 3.7;
}

.logo-github {
    margin-left: $padNavElem;
    &:before {
        @extend .icon;
        content: '\e950';
        font-size: 1.5em;
    }
}

.icon-menu {
    display: none; // Set to inline-block in responsive styles
    &:before {
        @extend .icon;
        content: '\e902';
        font-size: 2em;
        vertical-align: middle;
    }
}

.l-graphic-boxes {
    @include align-items(stretch);
    .col {
        @include display(flex);
        @include flex-direction(column);
        @include flex-wrap(nowrap);
        position: relative;
    }
    &.bg {
        background-color: #ccc;
        .col:not(:first-child) {
            border-left: 1px solid $clrGraphicBoxBorder;
            font-size: 0.9em;
        }
    }
}

.t-list-steps .code-wrapper {
    &.dev-prereqs {
        background-image: url("../images/logo-dev-prereqs.png");
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        height: 100px;
    }
    .btn.major {
        text-transform: uppercase;
    }
}

.t-footer {
    $c: #999;
    $logoW: 65px;
    @include linear-gradient(rgba(black, 0.4), transparent 15px);
    color: $c;
    padding-bottom: 200px;
    line-height: 140%;
    b { color:lighten($c, 20%); }
    .t-footer-content {
        font-size: 0.8em;
        padding-left: $logoW + 40px;
        position: relative;
        &:before {
            @extend .logo-nasa;
            content: '';
            display: block;
            height: $logoW;
            position: absolute;
            left: $marginH;
        }
    }
}

#lektor-edit-link {
    font-size: 1em !important;
    width: 20px !important;
}

/************************************************* DOCUMENTATION */
code {
    background-color: #eee;
    border-radius: 2px;
    display: inline-block;
    padding: 1px 5px;
}

.documentation-toc {
    line-height: 100%;
    ul {
        //font-size: 0.9em;
        color: lighten($clrBodyBg, 30%);
        list-style-type: none;
        padding: 0;
        li a {
            border-left: 3px solid transparent;
            display: inline-block;
            margin-bottom: 4px;
            padding: 3px 5px 3px 5px;
            &.current {
                border-color: #00aa00;
                font-weight: bold;
            }
            &:hover {
                border-color: rgba(#000, 0.05);
            }
        }
        ul {
            padding: 0 0 0 15px;
        }
    }
}

/************************************************* WHO'S USING OPEN MCT */
.l-using-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-auto-rows: 280px; // Increase once we have description and links
    grid-gap: 30px;
}

.c-using-card {
    border-radius: 10px;
    box-shadow:  rgba(0, 0, 0, 0.4) 0 2px 10px;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;

    &__image {
        flex: 0 0 auto;
        height: 150px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-clip: content-box;
    }

    &__content {
        padding: 1em;

        > * + * { margin-top: .5em; }
    }

    &__title,
    &__center {
        font-family: 'Montserrat', sans-serif;
    }

    &__title {
        font-size: 1.2em;
    }

    &__center {
        color: #666;
        font-size: 0.9em;
        text-transform: uppercase;
    }

    &__description {
        display: none; // TEMP
    }
}


/************************************************* PLUGINS */
.cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-auto-rows: auto;
    grid-gap: 30px;
}

.card {
    border-radius: 10px;
    box-shadow:  rgba(0, 0, 0, 0.4) 0 2px 10px;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.card-icon {
    padding-bottom: 50%;
    background-size: cover;
    background-position: center center;
    border-radius: 10px 10px 0px 0px;
}

.card-tag {
    transform: translateY(-50%);
}

.card-experimental {
    background: orange;
    color: white;
    float: left;
    padding: 0.5em 1em;
    border-radius: 0px 5px 5px 0px;
    font-size: 0.9em;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.03em;
}

.card-stable {
    float: left;
    color: green;
    background: #adebad;
    padding: 0.5em 1em;
    text-transform: uppercase;
    font-size: 0.9em;
    border-radius: 0px 5px 5px 0px;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    letter-spacing: -0.03em;
}

.card-content {
    padding: 0 1em 1em 1em;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    > * + * {
        margin-top: 5px;
    }

    p {
        margin-block-end: 0;
    }
}

.card-description {
    display: block;
    flex: 1;
}

.plugin-installation {
    display: block;
    margin: 15px 0 10px 0;
    font-size: 0.9em;
    word-break: break-all;

    code {
        padding: 5px 8px;
        -webkit-user-select: all;  /* Chrome 49+ */
        -moz-user-select: all;     /* Firefox 43+ */
        -ms-user-select: all;      /* No support yet */
        user-select: all;

        &:hover {
            box-shadow: inset rgba(black, 0.4) 0 1px 3px;
        }
    }
}

.card-button {
  font-size: 0.9em;
  margin-left: auto;
  margin-top: 20px;
  display: flex;

  .button-divider {
    padding: 0px 5px;
  }
}

a.card-link {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    letter-spacing: -0.03em;
    display: flex;
}

.card-logo-github {
  @extend .logo-github;
  content: '\e950';
  font-size: 0.9em;
}

.card-label {
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 0.8em;
    color: #999;
    text-transform: uppercase;
}

.plugins-code-wrapper {
    padding: 0.6em 0em;
}

.plugins-code {
  background-color: #333;
  border-radius: $radiusBasic;
  border: none;
  box-sizing: border-box;
  color: #ccc;
  display: block;
  font-family: monospace;
  padding: 10px 10px;
  width: 100%;
  line-height: 1.5;
  overflow: auto;
  white-space: nowrap;

  //enable one click select all
  -webkit-user-select: all;  /* Chrome 49+ */
  -moz-user-select: all;     /* Firefox 43+ */
  -ms-user-select: all;      /* No support yet */
  user-select: all;

  &:focus {
      outline: none;
  }
  &::selection {
      background-color: $clrLink;
      color: white;
  }

  &::-moz-selection {
      background-color: $clrLink;
      color: white;
  }


  .plugins-code-comment {
    color: #017ac6;

    &:focus {
        outline: none;
    }
    &::selection {
        background-color: $clrLink;
        color: white;
    }

    &::-moz-selection {
        background-color: $clrLink;
        color: white;
    }
  }

}

.plugins-inline-code {
  background-color: #333;
  border-radius: $radiusBasic;
  border: none;
  box-sizing: border-box;
  color: #ccc;
  display: inline-flex;
  font-family: monospace;
  font-size: 0.9em;
  padding: 3px 5px;
  margin: 2px 0px;
  overflow: auto;
  white-space: nowrap;

  //enable one click select all
  -webkit-user-select: all;  /* Chrome 49+ */
  -moz-user-select: all;     /* Firefox 43+ */
  -ms-user-select: all;      /* No support yet */
  user-select: all;

  &:focus {
      outline: none;
  }
  &::selection {
      background-color: $clrLink;
      color: white;
  }

  &::-moz-selection {
      background-color: $clrLink;
      color: white;
  }
}
