html, body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    line-spacing: normal;
    margin: 0;
    padding: 0;
}

div, li {
    box-sizing: border-box;
}

h1, h2, h3,
.h1, .h2, .h3 {
    line-height: 1;
    margin-top: 1em;
    margin-bottom: 0.3em;
}

iframe {
    border: none;
    display: block;
}

p, ol {
    margin: 0 0 $interiorMargin 0;
}

ol, ul {
    margin: 0;
}

ul.no-bullets {
    margin: 0 !important;
    padding: 0 !important;
    li {
        list-style: none;
    }

}
