@mixin respCols() {
    flex-direction: column;
    .col {
        margin: 0;
        padding: 0;
        width: 100% !important;
    }

    &.cols-equal {
        .col {
            flex: 1 1 auto !important;
        }

        .col + .col {
            margin-top: 1em;
        }
    }

    &.cols--with-dividers {
        .col {
            border-left: none !important;
        }
    }
}


/************************** STYLES */
@media only screen and (max-width: $mobileBreakLg) {
    body {
        min-width: inherit !important;
    }

    nav {
        height: auto !important;
        position: relative;
    }

    .l-main { padding-top: 0; }

    .main-nav {
        flex-wrap: wrap;
    }

    .mobile-hide {
        display: block;
    }

    .l-nav-links {
        border-top: 1px solid rgba(#fff, 0.1);
        justify-content: flex-start;
        margin-bottom: 10px;
        padding: 5px 0;
        width: 100%;
    }

    .l-page-section {
        max-width: inherit !important;
    }

    .cols:not(.cols--break-sm) {
        @include respCols();
        .col {
            &.documentation {
                // Allows documentation-toc to go first
                margin-top: 20px;
                order: 2;
            }
            &.documentation-toc h2 {
                margin-top: 0;
            }
        }
    }

    .l-graphic-boxes {
        .cols {
            .col {
                &:not(:first-child) {
                    border-top: 1px solid $clrGraphicBoxBorder;
                }
                border-left: none !important;
                display: block;
                padding: $marginH 0;
                margin-bottom: $marginH 0;
                .c-wrap.graphic {
                    text-align: center;
                    .graphic {
                        display: inline-block;
                        min-width: 220px;
                        width: 65%;
                    }
                }
            }
        }
    }

    .toc-header {
        &:after {
            @include transform(rotate(90deg));
            @include trans-prop-nice($props: rotate);
            display: inline-block;
            font-family: 'omct-symbols' !important;
            font-size: 0.7em;
            content: '\e900';
            line-height: 100%;
            margin-left: 5px;
        }
        &.expanded:after {
            @include transform(rotate(270deg));
        }
    }

    .toc-content {
        @include expandCollapseV();
        @include trans-prop-nice($props: (height, max-height));
        &.expanded {
            @include expandCollapseV('expanded');
        }
    }
    /************************** HEROES */
    .l-hero-holder .l-hero-main .cols {
        padding: 32px !important;
    }

    /************************** PLUGINS */
    .cards {
        grid-gap: 60px;
    }
}

@media only screen and (max-width: $mobileBreakSm) {
    .cols--break-sm {
        @include respCols();
    }

    .mobile-sm-only {
        display: inherit;
    }
    .icon-menu {
        display: inline-block;
    };
    .logo-github {
        display: none;
    }
    .l-nav-links {
        background: lighten($clrBodyBg, 10%);
        box-sizing: border-box;
        display: block;
        line-height: 250%;
        padding: 0 $marginH;
        position: absolute;
        text-align: left;
        top: 100%; left: 0; right: 0;
        @include expandCollapseV();
        .nav-link {
            &:not(:first-child) { border-top: 1px solid rgba(#fff, 0.2); }
            display: block;
            margin: 0;
        }
        &.expanded {
            @include expandCollapseV($mode: 'expanded');
            box-shadow: 0 2px 20px rgba(#000, 0.6);
            padding: 10px $marginH;
        }
    }
    .icon-menu {
        flex-grow: 9;
        text-align: right;
    }
    /************************** HEROES */
    .l-hero-holder {
        font-size: 0.8em;
    }
    /************************** PLUGINS */
    .cards {
        grid-gap: 40px;
    }
}
