// Home page Developed By section
.c-developed-by {
    .col {
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
    }

    h2 {
        display: flex;
        margin-bottom: .7em;
    }

    .c-wrap-graphic {
        display: flex;
        align-items: center;
        flex: 1 1 auto;

        * + * {
            margin-left: 3em;
        }
    }

    &[class*='__logo'] {
        background-repeat: no-repeat;
        background-size: contain;
        display: block;
    }
}

@media only screen and (max-width: $mobileBreakSm) {
    .c-developed-by {
        .col + .col { margin-top: 3em !important; }
    }
}