.jsdoc nav
{
    display: block;
    float: right;
    margin-top: 28px;
    width: 30%;
    box-sizing: border-box;
    border-left: 1px solid #ccc;
    padding-left: 16px;

    ul {
        font-size: 100%;
        line-height: 17px;
        padding: 0;
        margin: 0;
        list-style-type: none;

        li {
            list-style: none;
            text-transform: none;
        }
    }
}
