.p-body {
    .l-page-section {
        &:not(:first-child) .wi {
            border-top: 1px solid rgba(black, 0.1);
        }
        &.no-margin {
            padding-top: $interiorMargin;
            padding-bottom: $interiorMargin;
        }
        h2,
        .h2 {
            font-size: 1.5em;
            text-align: left;
        }
        h2:first-child,
        .h2:first-child {
            margin-top: inherit;
        }
    }
}

img.img-medium {
    width: 640px;
}

img.img-small {
    width: 320px;
}

ul.t-list-steps {
    padding: 0;
}

.l-page-section {
    ul {
        padding: 0 0 20px 40px;
    }

    img {
        max-width: 100%;
    }
}
