.l-hero-holder {
    position: relative;
    .l-hero-main {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        top: 0; right: 0; bottom: 0; left: 0;
        line-height: 230%;
        position: absolute;
        &.trans-to-next {
            @include trans-prop-nice-fade($dur: 1s);
        }

        .l-hero-text-bg-fade {
            @include linear-gradient(rgba(black, 0), rgba(black, 0.4) 50%);
            position: absolute;
            top: auto;
            right: 0;
            bottom: 0;
            left: 0;
            min-height: 45%;
        }
        .l-hero-text {
            @include align-items(center);
            z-index: 2;
            color: #ccc;
            * :not(.btn) {
                text-shadow: rgba(black, 0.6) 0 1px 10px;
            }

            a, b {
                color: white;
            }
        }
    }
}

.p-home .l-hero-holder {
    height: 540px;
    .l-hero-main {
        .cols {
            padding: 0 $marginH * 2;
        }
    }
}
.p-body .l-hero-holder {
    height: 200px;
    text-transform: uppercase;
    .l-hero-main {
        &:before {
            @include linear-gradient(rgba($clrLink, 1) 10%, rgba($clrLink, 0.3));
            background-color: transparent; // Need this because bourbon's linear-gradient mixin sucks
            content: '';
            position: absolute;
            top: 0; right: 0; bottom: 0; left: 0;
            z-index: 1;
        }
        .cols {
            padding-top: 100px;
            .col {
                z-index: 1;
            }
        }
    }
}

/******************************************** SPECIFIC HEROES */
.p-home {
    .hero-overview {
        background-image: url('../images/hero-rp-dot-1.jpg');
        .col-left {
            font-size: 1.4em;
        }
        .col-right {
            text-align: center;
            text-transform: uppercase;
        }
    }

    .hero-intern2017 {
        background-image: url('../images/hero-bg-apollo-moon-2.jpg');
        .col-left {
            min-width: 50%;
            img { width: 100%; }
        }

        .l-hero-text-bg-fade {
            @include linear-gradient(rgba(black, 0), rgba(black, 0.3) 40%);
            min-height: 75%;
        }
    }

    .hero-comiccon2017 {
        background-image: url('../images/NASA_journeytomars2.jpg');
        a.comiccon2017-logo {
            display: inline-block;
            min-width: 230px;
            img {
                display: inline-block;
                vertical-align: middle;
                &.logo_icon {
                    max-width: 70px;
                    max-height: 91px;
                }
                &.logo_type {
                    margin-left: 15px;
                    max-width: 135px;
                    max-height: 50px;
                }
            }
        }
        .col-left {
            min-width: 50%;
        }

        .l-hero-text-bg-fade {
            @include linear-gradient(rgba(black, 0), rgba(black, 0.3) 40%);
            min-height: 75%;
        }
    }
}

.p-body {
    &.about-open-mct .l-hero-holder .l-hero-main { background-image: url('../images/hero-body-packets-1.jpg'); }
    &.getting-started .l-hero-holder .l-hero-main { background-image: url('../images/hero-body-panel-1.jpg'); }
    &.contributing .l-hero-holder .l-hero-main { background-image: url('../images/hero-body-stacked-plot-1.jpg'); }
    &.documentation .l-hero-holder .l-hero-main { background-image: url('../images/hero-body-timeline-1.jpg'); }
    &.plugins .l-hero-holder .l-hero-main { background-image: url('../images/hero-body-layout-traverse.jpg'); }
}
